    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 Clasificación y distribución de comunidades étnicas en Colombia

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-8.mb-4.mb-md-0.my-2
        p.mb-4 Colombia es uno de los países con mayor diversidad cultural y étnica de América Latina. Según el Departamento Administrativo Nacional de Estadística (DANE, Censo Nacional de Población y Vivienda, 2018), cerca del 13 % de la población se autoreconoce como parte de un grupo étnico, lo que equivale a más de 4,5 millones de personas.
        p.mb-4 Esta diversidad, señala el Ministerio de Cultura (2010), constituye la base de la identidad nacional y representa una oportunidad para promover procesos de desarrollo sostenible y turismo sostenible, en armonía con la protección cultural y el respeto por la diferencia.
        p.mb-4 El Estado colombiano, a través de la Constitución Política de 1991 y de normas complementarias, reconoce cuatro grandes grupos étnicos: los pueblos indígenas, las comunidades afrodescendientes, el pueblo Rrom (gitano) y las comunidades raizales del Archipiélago de San Andrés, Providencia y Santa Catalina.
      .col-md-4
