      h3
      p.mb-4 En Colombia, los pueblos indígenas constituyen una parte esencial del patrimonio cultural, espiritual y ambiental de la nación. Están conformados por más de cien comunidades, cada una con identidades, lenguas, tradiciones y cosmovisiones propias, que expresan una profunda conexión con la naturaleza y una concepción integral del territorio como espacio de vida, memoria y equilibrio.

      .row.justify-content-center.align-items-center.mb-2(data-aos="flip-up")
        .col-md-4.mb-4.mb-md-0.my-2
          h3.color-adicional-4 Su presencia se distribuye por todo el territorio nacional, con una mayor concentración en departamentos como La Guajira, Cauca, Nariño, Amazonas, Vaupés, Putumayo, Guaviare y Chocó, regiones donde la diversidad cultural se entrelaza con una riqueza ambiental excepcional.
