      p-mb-4 Cada pueblo indígena posee formas particulares de organización social, sistemas de gobierno propio, saberes ancestrales y prácticas tradicionales que orientan su relación con el entorno. Estas comunidades desarrollan actividades sostenibles como la agricultura ecológica, la pesca artesanal, la medicina tradicional y la artesanía, las cuales no solo garantizan su subsistencia, sino que también contribuyen a la conservación de los ecosistemas y al mantenimiento del equilibrio territorial.

