    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-right")
      p.mb-4 Según la Organización Mundial del Turismo (OMT), alrededor de 370 millones de personas pertenecientes a pueblos indígenas en el mundo están vinculadas a actividades turísticas. Cuando se gestiona de manera responsable y sostenible, el turismo liderado por comunidades indígenas puede generar empleo, reducir la pobreza, fortalecer la autonomía comunitaria, revitalizar las expresiones culturales y promover una relación armónica y duradera entre las comunidades, la tierra y la naturaleza.
      p.mb-4 En este contexto, el turismo comunitario desarrollado en territorios indígenas se consolida como una oportunidad para el intercambio cultural y la valoración de los saberes ancestrales, siempre que se realice con respeto, participación activa y equidad en la distribución de beneficios. De esta manera, los pueblos indígenas desempeñan un papel esencial en la construcción de un modelo de turismo sostenible y responsable, que reconoce la diversidad cultural como un pilar para el desarrollo equilibrado e inclusivo del país.

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img5.svg', alt='Texto que describa la imagen')
      .col-md-8.mb-4.mb-md-0.my-2
        h4.mb-4 Comunidades afrodescendientes
        p.mb-4 Las comunidades afrodescendientes en Colombia constituyen una parte esencial del tejido social y cultural del país. Herederas de tradiciones africanas que, a lo largo de los siglos, se han fusionado con elementos indígenas y europeos, estas comunidades han construido una identidad propia basada en la resistencia, la solidaridad y el respeto por la naturaleza. Su presencia se concentra principalmente en las regiones del Pacífico —particularmente en los departamentos de Chocó, Valle del Cauca, Cauca y Nariño— y en la región Caribe, con destacada representación en Bolívar, Atlántico y Magdalena.
        p.mb-4 A continuación se ampliará la comprensión de la importancia de esta comunidad para el país, así como, la importancia que tiene ante la comunidad internacional:

    br 
