          .col-xl-10
            h3.letra-acordeon-titulos Prácticas sostenibles comunitarias
            p.letra-acordeon-titulos Los pueblos afrodescendientes mantienen una relación profunda con su entorno natural, especialmente con los ríos, los manglares, las selvas y el mar, considerados fuentes de vida, sustento y espiritualidad. A través de actividades tradicionales como la pesca artesanal, la agricultura de pancoger, la minería ancestral y la elaboración de artesanías, estas comunidades conservan prácticas sostenibles que reflejan un conocimiento ancestral del territorio.
      
      .tarjeta.p-4(titulo="Diversidad cultural")
