            h3.letra-acordeon-titulos Diversidad cultural
            p.letra-acordeon-titulos La música, la danza, la gastronomía y las festividades constituyen expresiones vivas de su herencia cultural y representan un valioso atractivo turístico para quienes buscan experiencias auténticas. Desde el currulao en el Pacífico hasta las celebraciones de San Basilio de Palenque y el Festival de Tambores en el Caribe, el legado afrodescendiente enriquece la diversidad cultural del país.canales accesibles y eficientes.
      
      .tarjeta.p-4(titulo="Conservación del patrimonio natural y cultural")
