            h3.letra-acordeon-titulos Conservación del patrimonio natural y cultural
            p.letra-acordeon-titulos En el ámbito del turismo, las comunidades afrodescendientes han fortalecido iniciativas de turismo comunitario orientadas a la conservación del patrimonio natural y cultural, generando al mismo tiempo oportunidades económicas locales. Este modelo de turismo sostenible busca fortalecer la identidad cultural, rescatar tradiciones y garantizar que los beneficios derivados de la actividad turística lleguen directamente a las familias y organizaciones comunitarias. Así, el turismo se convierte en una herramienta de empoderamiento social y preservación de la memoria colectiva afrocolombiana.servicios incluidos.

      .tarjeta.p-4(titulo="Ecoturismo y turismo comunitario")
        .row
          .col-xl-2.mb-4
