          .col-xl-10
            h3.letra-acordeon-titulos Ecoturismo y turismo comunitario
            p.letra-acordeon-titulos Cabe destacar que el turismo afrocolombiano no se limita únicamente a la celebración de festividades o tradiciones culturales, sino que también abarca la visita a espacios naturales donde estas comunidades han coexistido durante siglos —como los ríos y manglares del Pacífico— ofreciendo experiencias de ecoturismo y turismo comunitario que promueven la sostenibilidad y el desarrollo local.

      .tarjeta.p-4(titulo="Prácticas sostenibles comunitarias")
        .row
          .col-xl-2.mb-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-ac5.svg', alt='Texto que describa la imagen')
              
          .col-xl-10
            h3.letra-acordeon-titulos Prácticas sostenibles comunitarias
            p.letra-acordeon-titulos En reconocimiento a su diversidad cultural y al fortalecimiento de las iniciativas turísticas con enfoque étnico, Colombia fue distinguida en abril de 2024 como el “Mejor Destino Turístico Afro del Mundo” durante los II Premios Afroturismo, realizados en el marco de la World Travel Market (WTM) Latinoamérica, en São Paulo, Brasil.
      
    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-right")
      p.mb-4 Este galardón resalta las experiencias auténticas, el valor del patrimonio inmaterial y la riqueza cultural de la herencia afrocolombiana, reflejada en destinos emblemáticos como Cali, Cartagena y el municipio de San Basilio de Palenque, reconocido por la UNESCO como Patrimonio Oral e Inmaterial de la Humanidad. Estos lugares ofrecen propuestas de turismo sostenible, inclusivo y comunitario que promueven el orgullo por la identidad afrodescendiente, el intercambio cultural y la generación de oportunidades económicas para las comunidades locales. Además, este reconocimiento internacional reafirma el papel de Colombia como referente mundial en el desarrollo de un turismo diverso, responsable y culturalmente significativo, que celebra la historia, la cultura y las tradiciones vivas de sus pueblos afrodescendientes.

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-8.mb-4.mb-md-0.my-2
        h3 Pueblo Rrom o Gitano
        p.mb-4 Aunque el pueblo Rrom habita en Colombia desde el siglo XVII, su reconocimiento oficial como grupo étnico con derechos colectivos se formalizó en el año 2010 mediante el Decreto 2957, que desarrolla los principios constitucionales orientados a la protección de la diversidad étnica y cultural de la nación. Este decreto reconoce la igualdad y dignidad de todas las culturas que conviven en el país y establece un marco normativo integral para la salvaguarda de los derechos del pueblo Rrom, incluyendo su identidad, lengua, costumbres, organización social y formas tradicionales de vida.
        p.mb-4 A continuación se amplía la forma en que el pueblo Rrom se ha mantenido en Colombia, así como los lugares en que se encuentran sus mayores asentamientos y su integración como parte de las actividades turísticas:
      .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img6.svg', alt='Texto que describa la imagen')
    
    br
    .tarjeta.tarjeta--azul.p-4.pt-3.tarjeta-slyder-tipo-a-b(data-aos="flip-down")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center.my-1
          .col-md-7.mb-4.mb-md-0
            h2.color-adicional-4 Históricamente de carácter nómada, el pueblo Rrom se ha adaptado a diferentes contextos sin perder su esencia cultural. Conserva su idioma —el romani—, sus tradiciones orales, sus rituales familiares y sociales, y una cosmovisión que valora profundamente la libertad, la movilidad y la solidaridad entre sus clanes o kumpanias. A pesar de los procesos de sedentarización vividos en las últimas décadas, continúan preservando prácticas culturales propias que los diferencian de otros grupos del país.

          .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img7.svg', alt='Texto que describa la imagen')

        .row.justify-content-center.align-items-center.my-1
          .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img8.svg', alt='Texto que describa la imagen')

          .col-md-7.mb-4.mb-md-0
            h2.color-adicional-4 En Colombia, las principales kumpanias Rrom se encuentran en Bogotá, Medellín, Cali, Barranquilla, Cúcuta y otras ciudades intermedias. En estos territorios, combinan sus oficios tradicionales —como el comercio ambulante, la música, la metalurgia, la artesanía y la talabartería— con actividades contemporáneas que les permiten participar activamente en la vida económica y social.


        .row.justify-content-center.align-items-center.my-1
          .col-md-7.mb-4.mb-md-0
            h2.color-adicional-4 El turismo se ha convertido en una oportunidad significativa para que el pueblo Rrom comparta su herencia cultural, a través de la difusión de su idioma, música, danzas y oficios tradicionales, promoviendo la preservación de su identidad y la inclusión social en distintos destinos del país. Cuando se desarrolla con un enfoque respetuoso, participativo y ético, este tipo de experiencias turísticas contribuye a visibilizar su historia, fortalecer el diálogo intercultural y combatir estereotipos asociados a su comunidad.

          .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img9.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p No obstante, persisten desafíos relacionados con la movilidad restringida, los procesos de urbanización forzada y los efectos del conflicto armado, los cuales han impactado su tradición nómada y han generado transformaciones en sus modos de vida y estrategias de subsistencia.
      p El reconocimiento jurídico, cultural y turístico del pueblo Rrom constituye un avance fundamental hacia la inclusión, la equidad y el respeto por la diversidad étnica, reafirmando el compromiso del Estado colombiano con la construcción de una sociedad pluralista, intercultural y respetuosa de las múltiples identidades que conforman la nación.
        
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
