      .col-md-9.mb-4.mb-md-0.my-4
        h3 Comunidades raizales del Archipiélago de San Andrés, Providencia y Santa Catalina
        p El pueblo raizal constituye un grupo étnico afrodescendiente originario del Archipiélago de San Andrés, Providencia y Santa Catalina, con una identidad cultural propia que combina elementos africanos, europeos y caribeños. Su historia se remonta a los siglos XVII y XVIII, cuando las islas fueron pobladas por africanos esclavizados y colonos ingleses, dando origen a una cultura anglo-caribeña singular, reflejada en su idioma, religión, tradiciones y organización social.
      
    br
    .bg--img_03(data-aos="fade-right")
      .row.justify-content-center
        .col-lg-12.mb-4.mb-md-0
          AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")
            .row.justify-content-center.align-items-center(titulo="Lengua tradicional")
              .col-1
              .col-7
                p.letra-acordeon-mas-menos La lengua tradicional de los raizales es el creole o creol sanandresano, una mezcla de inglés, español y lenguas africanas que constituye un símbolo de identidad y cohesión comunitaria. Además, muchos raizales son bilingües o trilingües, ya que dominan el inglés y el español, lo que evidencia su herencia multicultural y su vínculo histórico con el Caribe anglófono.
              
              .col-md-4
                figure
                  img(src='@/assets/curso/temas/tema1/tema1-ce-img11.svg', alt='Texto que describa la imagen')

            .row.justify-content-center.align-items-center(titulo="Prácticas culturales")
              .col-1
              .col-7
                p.letra-acordeon-mas-menos Las comunidades raizales mantienen fuertes lazos culturales afrocaribeños, expresados en su música (como el calipso, el reggae o el mentó), sus danzas, su gastronomía basada en productos del mar y el coco, y sus celebraciones religiosas y comunitarias, entre las que destacan los festivales tradicionales y las ceremonias vinculadas a la fe protestante. Estas manifestaciones culturales constituyen un patrimonio inmaterial de gran valor para la nación colombiana.

              .col-md-4
                figure
                  img(src='@/assets/curso/temas/tema1/tema1-ce-img12.svg', alt='Texto que describa la imagen')

            .row.justify-content-center.align-items-center(titulo="Conexión territorial")
              .col-1
              .col-7
                p.letra-acordeon-mas-menos En el ámbito social, el pueblo raizal se caracteriza por una profunda conexión con el territorio insular y marino, al que considera parte esencial de su identidad. Su modo de vida tradicional se relaciona con la pesca artesanal, la agricultura de subsistencia y el comercio local. En las últimas décadas, el turismo se ha consolidado como una actividad económica relevante; sin embargo, los raizales promueven un modelo de turismo sostenible y culturalmente responsable, que reconozca su papel como guardianes del patrimonio natural y cultural del archipiélago.
            
              .col-md-4
                figure
                  img(src='@/assets/curso/temas/tema1/tema1-ce-img13.svg', alt='Texto que describa la imagen')
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p A pesar de su riqueza cultural, las comunidades raizales enfrentan desafíos significativos relacionados con la presión demográfica, la pérdida de territorio, el impacto ambiental y la necesidad de preservar su lengua y tradiciones frente a la influencia del turismo masivo y la globalización. Por ello, el Estado colombiano, mediante diversos instrumentos normativos y de política pública, ha reconocido sus derechos colectivos y su autonomía cultural, reafirmando el compromiso con la protección y fortalecimiento de su identidad étnica.
      h3 Distribución Geográfica
      p Conocer la clasificación y distribución de las comunidades étnicas en Colombia es fundamental para diseñar servicios turísticos sostenibles que respeten sus derechos colectivos, promuevan la interculturalidad y fortalezcan el reconocimiento de la diversidad cultural del país. Este conocimiento resulta esencial al momento de planificar recorridos o experiencias turísticas en territorios habitados por comunidades étnicas, garantizando que las actividades se desarrollen con pertinencia cultural y respeto por sus formas de vida.
      p A continuación, se presenta —mediante un análisis cartográfico— la distribución geográfica de los resguardos indígenas y de los territorios con presencia significativa de comunidades afrodescendientes, entendiendo por estas últimas aquellas regiones en las que esta población representa más del 80 % del total demográfico local. Este tipo de análisis permite identificar los espacios socioculturales y ambientales donde el turismo puede contribuir a la conservación del patrimonio natural y cultural, así como al fortalecimiento de las economías comunitarias.
          
    .row.justify-content-center(data-aos="fade-right")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img14.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p Es fundamental establecer una diferenciación entre las comunidades afrocolombianas y las comunidades raizales, ya que, aunque comparten raíces africanas y una historia común de resistencia y defensa de sus derechos, poseen particularidades culturales y geográficas que las distinguen. La principal diferencia radica en su ubicación territorial y en el contexto histórico-cultural en el que se desarrollaron.
      p A diferencia del mapa anterior, en el siguiente mapa se presenta la ubicación de las comunidades raizales reconocidas por la Constitución Política de 1991, las cuales —como se mencionó previamente— se concentran mayoritariamente en el Archipiélago de San Andrés, Providencia y Santa Catalina, territorio insular del Caribe colombiano del cual son originarias.
    
