    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p Por su parte, según el análisis de la cartografía de distribución poblacional elaborada por el Departamento Administrativo Nacional de Estadística (DANE), el pueblo Rrom se encuentra distribuido a lo largo de la cordillera de los Andes y en diversos puntos de la región Caribe del país, donde registra una presencia significativa, tal como se aprecia en el siguiente mapa.
    
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img16.svg', alt='Texto que describa la imagen')
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-down")
      p Las figuras anteriores permiten evidenciar que las regiones donde se localiza la mayor parte de las comunidades étnicas de Colombia coinciden con territorios de alto valor ambiental, paisajístico y cultural, lo que las convierte en zonas con un importante potencial turístico. En estos espacios pueden desarrollarse proyectos turísticos sostenibles que promuevan el bienestar comunitario y la conservación de los ecosistemas.
      p En el caso de las zonas con mayor concentración de resguardos indígenas, ubicadas principalmente en el suroccidente del país —departamentos como Amazonas, Guainía, Vaupés y Caquetá—, así como en resguardos de reconocida importancia cultural internacional, como los de la Sierra Nevada de Santa Marta (Arhuacos, Kogui, Wiwa y Kankuamo), se identifica una relación más estrecha con el turismo de naturaleza, el turismo cultural y el turismo de aventura. En estos escenarios, los visitantes se sienten atraídos por aprender sobre la cosmovisión, las tradiciones y los entornos naturales en los que conviven estas comunidades, dinámica que también comparten muchas comunidades afrodescendientes del Pacífico y algunas comunidades Rrom asentadas en las zonas andinas.
      p Por su parte, las comunidades raizales del Caribe colombiano, particularmente en el Archipiélago de San Andrés, Providencia y Santa Catalina, reciben un tipo de turismo más convencional y masivo, caracterizado por un fuerte interés en el disfrute de sus paisajes costeros y playas paradisíacas. Este modelo, aunque económicamente relevante, puede resultar invasivo e impactante para los ecosistemas insulares y las dinámicas culturales locales, planteando retos significativos en materia de sostenibilidad y manejo responsable del turismo a largo plazo


